const DEFAULT_OPTIONS = {
  selectable: false,
  strokeWidth: 2,
  evented: false,
  stroke: 'red',
  text: '',
  textStyle: 'red',
  // strokeDashArray: [5, 5],
}

export default class FabricArrowLine extends fabric.Line {
  constructor(points, options = {}) {
    super(points, {
      ...DEFAULT_OPTIONS,
      ...options
    })
  }
  // 
  getLength () {
    var p = this.calcLinePoints()
    const xDiff = p.x2 - p.x1
    const yDiff = p.y2 - p.y1
    return Math.sqrt(Math.pow(xDiff, 2) + Math.pow(yDiff, 2))
  }
  _render (ctx) {
    super._render(ctx)
    // 
    var p = this.calcLinePoints()
    const xDiff = p.x2 - p.x1
    const yDiff = p.y2 - p.y1
    if (!this.visible || (!xDiff && !yDiff)) return
    
    const angle = Math.atan2(yDiff, xDiff)

    ctx.strokeStyle = this.stroke
    if (this.strokeDashArray) {
      ctx.setLineDash(this.strokeDashArray)
    }

     // 
    ctx.save()
    ctx.translate((p.x2 - p.x1) / 2, (p.y2 - p.y1) / 2)
    ctx.rotate(angle)
    ctx.moveTo(-8, 8)
    ctx.lineTo(0, 0)
    ctx.lineTo(-8, -8)
    ctx.stroke()
    ctx.restore()

    // 
    ctx.save()
    ctx.translate(-(p.x2 - p.x1) / 2, -(p.y2 - p.y1) / 2)
    ctx.rotate(angle)
    ctx.moveTo(8, 8)
    ctx.lineTo(0, 0)
    ctx.lineTo(8, -8)
    ctx.stroke()
    ctx.restore()
    // 
    ctx.save()
    // 
    ctx.rotate(xDiff > 0 ? Math.atan2(yDiff,xDiff) : Math.atan2(-yDiff,-xDiff))

    // 
    ctx.fillStyle = this.textStyle
    ctx.textAlign = (xDiff <= 0 ? 'left' : 'right')
    ctx.fillText(this.text, 0, -10)
    ctx.restore()
  }
}
